import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const state = {
	arr:[]
}
const getters = {
	arr_getters(state){
		return state.arr
	},
	z_getters(state){
		var num = 0
		for(var i=0;i<state.arr.length;i++){
			num+=state.arr[i].price*state.arr[i].z
		}
		return num
	},
}

const mutations = {
	 insert_mutations(state,data){
	 	state.arr.push(data)
	 },
	 jia_mutations(state,data){
	 	
	 	var num = 0
	 	for(var i=0;i<state.arr.length;i++){
	 		if(state.arr[i].id == data){
	 			num = i
	 		}
	 	}
	 	state.arr[num].z++
	 },
	 jian_mutations(state,data){
	 	
	 	var num = 0
	 	for(var i=0;i<state.arr.length;i++){
	 		if(state.arr[i].id == data){
	 			num = i
	 		}
	 	}
	 	state.arr[num].z--
	 	if(state.arr[num].z<=1){
	 		state.arr[num].z = 1
	 	}
	 },
	 delete_mutations(state,data){
	 	var num = 0
	 	for(var i=0;i<state.arr.length;i++){
	 		if(state.arr[i].id == data){
	 			num = i
	 		}
	 	}
	 	state.arr.splice(num,1)
	 }
}
const actions = {
	insert_actions({commit},data){
		commit('insert_mutations',data)
	},
	jia_actions({commit},data){	
		commit('jia_mutations',data)
	},
	jian_actions({commit},data){
		commit('jian_mutations',data)
	},
	delete_actions({commit},data){
		commit('delete_mutations',data)
	}
}

export default new Vuex.Store({
	state,
	getters,
	mutations,
	actions
})



