                        <p style="text-align: left;margin-left:10px;color:#444!important;font-size: 12px;">短信验证码<span style="color:red;margin-left:10px;">*</span><input type="text" style="border:1px #bfbfbf solid;outline:none;width:235px;height:30px;margin-left:88px;border-radius:2px;background: #f5f5f5!important;"><button style="width:50px;border:none;background: #000;color:#fff;padding:6px 0">获取</button></p>
                        <p style="text-align: left;margin-left:10px;color:#444!important;font-size: 12px;">密码<span style="color:red;margin-left:10px;">*</span><input type="text" style="border:1px #bfbfbf solid;outline:none;width:285px;height:30px;margin-left:122px;border-radius:2px;background: #f5f5f5!important;padding:3px;"></p>
