<template>
	<div>
		<div class="big">
			<div class="big_car">
				<div v-if="arr_getters.length >0">
					<div class="success">
						<p>
							<i class="glyphicon glyphicon-ok"></i>
							<b style="font-weight: normal;">成功添加进您的购物车</b>
						</p>
						<router-link to='/online'><button style="background-color: #10ac62;font-size:14px;border:none;color:#fff;width:98px;height:36px;margin-top: -10px;">继续购物</button></router-link>
					</div>
					<div class="all" style="display: flex;">
						<div class="table_left" style="width:60%">
							<div class="table_top" style="border-bottom:3px solid #dddddd;width:100%;">
							<span style="font-size: 16px;font-weight:bold;padding-bottom: 10px;font-family:微软雅黑;padding-right:50%;">商品</span>
							<span style="font-size: 16px;font-weight:bold;padding-bottom: 10px;font-family:微软雅黑;">价格</span>
							<span style="font-size: 16px;font-weight:bold;padding-bottom: 10px;font-family:微软雅黑;padding-left: 5%;">数量</span>
							<span style="font-size: 16px;font-weight:bold;padding-bottom: 10px;font-family:微软雅黑;text-align: right;padding-left:20%;">合计</span>
							</div>			
							<div v-for="i in arr_getters">
								<div class="buycar">
									<div class="table_buy" style="width:100%;height:auto;margin-bottom:10px;">
										<div class="list" style="width:100%;height:auto;border-bottom:1px dotted #bfbfbf">
											<p style="width:5%;position:relative;left:0;top:0;font-size: 18px;"><i class="glyphicon glyphicon-remove-sign" style="color: #85bca5;" @click="delete_actions(i.id)"></i></p>
											<span style="width:28%;height:auto;padding:10px;"><img :src="i.img" style="width:200px;height: 200px; margin-left: 10px;margin-bottom: 10px;">
											</span>
											<span style="color: #666;margin-bottom: 6px;font-size:15px;">{{i.name}}</span>
											<span style="color: #000;font-family:微软雅黑;font-size:16px;padding-left: 1%;">
												￥{{i.price}}
											</span>
											<span style="margin-left: 10px;">
												<button style="width:20px;height: 28px;background:none;border:1px solid #eee;outline:none;color: #8d9095;" @click="jian_actions(i.id)">-</button><span style="width:36px;height: 28px;display:bold;border:1px solid #eee;line-height: 28px;text-align: center;padding:5px 20px">{{i.z}}</span><button style="width:20px;height: 28px;background:none;border:1px solid #eee;outline:none;color: #8d9095;" @click="jia_actions(i.id)">+</button>
											</span>
											<span style="padding-left:40px;font-size: 16px;color: #595959;">￥{{i.z*i.price}}</span>
										</div>
									</div>
								</div>							
							</div>
						</div>
						<div class="shop2" style="padding-left:30px;width:40%;">
							<table class="shop_table" cellspacing="0" style="width:100%;">
								<thead style="border-bottom: 3px solid #ddd;">
									<tr>
										<th style="width: 100%;font-size: 18px;">购物车总计</th>
									</tr>
								</thead>
							</table>
							<div class="ok">
								<p>
									<span>购物车小计</span>
									<span>{{z_getters}}</span>
								</p >
								<p>
									<span>运费</span>
									<span>免费送货</span>
								</p >
								<p style="border-bottom: 3px solid #ddd;border-top: 1px solid #ddd;">
									<span>订单总计</span>
									<span>{{z_getters}}</span>
								</p >
								<input class="ok_input" type="text" value="前往收银台" />
								<p style="border-bottom: 3px solid #ddd;">
									<span>优惠券:</span>
								</p >
								<input class="ok_input1" type="text" value="优惠券代码" />
								<input style="color:#2baf7b;border-color: #2baf7b;background: #fff;" class="ok_input" type="text" value="使用优惠券" />
							</div>
						</div>						
					</div>
				</div>
				<div v-else>
					<div class="new">
						<i class="glyphicon glyphicon-ok"></i>
						<b style="font-weight: normal;margin-left: 10px;">购物车已更新。</b>
					</div>
					<p style="text-align: center;font-size: 16px;color: #666666;margin-top:50px;">您的购物车目前是空的</p>
					<div class="back" style="border:1px solid #2baf7b; background-color: transparent;width:100px;height:40px;line-height:40px;text-align: center;margin:0px auto;margin-bottom:100px;">
						<i class="glyphicon glyphicon-arrow-left" style="color:#2baf7b"></i>
						<router-link to="/order">
							<b style="font-size: 15px;font-weight: normal;color: #2baf7b;">返回商店</b>
						</router-link>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script> 
	import {mapGetters,mapActions} from 'vuex'
	export default{
		computed:mapGetters(['arr_getters','z_getters']),
		methods:mapActions(['jia_actions','jian_actions','delete_actions'])
	}
</script>

<style>
	.big{
		width: 100%;
		height:auto;
	}
	.big_car{
		width:1000px;
		margin:0 auto;
		height:auto;
	}
	.new{
		width: 974px;
		height: 53px;
		border:1px solid #88dfae!important;
		color: #2f9a4c;
		font-size: 16px;
		padding: 15px 8px;
		margin-top:40px;
	}
	.success{
		width: 974px;
		height: 53px;
		border:1px solid #88dfae!important;
		color: #2f9a4c;
		font-size: 16px;
		padding: 15px 8px;
		margin:40px auto;
		display: flex;
		justify-content: space-between;
	}
	.buycar{
		max-width:1000px;
		height:auto;		
		margin:0 auto;
		display: flex;
	}
	.ok p {
		width: 100%;
		height: 40px;
		line-height: 40px;
		display: flex;
		justify-content: space-between;
		margin-bottom: 0;
	}
	
	.ok_input,
	.ok_input1 {
		width: 100%;
		height: 40px;
		text-align: center;
		line-height: 40px;
		font-size: 14px;
		color: #ffffff;
		border: 1px solid #2baf7b;
		background-color: #2baf7b;
		margin: 15px 0;
	}
	
	.ok_input1 {
		margin-top: 15px!important;
		margin-bottom: 0;
		background: #fff;
		padding: 9px 10px 8px 10px;
		text-align: left;
		border: solid 1px #e0e0e0;
		color: #7a7a7a;
	}
	
	.shu>button {
		border: 1px solid #CCCCCC;
		padding: 3px;
	}
</style>
<div class="shop2" style="padding-left:30px;width:40%;">
								<table class="shop_table" cellspacing="0" style="width:100%;">
									<thead style="border-bottom: 3px solid #ddd;">
										<tr>
											<th style="width: 100%;font-size: 18px;">购物车总计</th>
										</tr>
									</thead>
								</table>
								<div class="ok">
									<p>
										<span>购物车小计</span>
										<span>{{z_getters}}</span>
									</p >
									<p>
										<span>运费</span>
										<span>免费送货</span>
									</p >
									<p style="border-bottom: 3px solid #ddd;border-top: 1px solid #ddd;">
										<span>订单总计</span>
										<span>{{z_getters}}</span>
									</p >
									<input class="ok_input" type="text" value="前往收银台" />
									<p style="border-bottom: 3px solid #ddd;">
										<span>优惠券:</span>
									</p >
									<input class="ok_input1" type="text" value="优惠券代码" />
									<input style="color:#2baf7b;border-color: #2baf7b;background: #fff;" class="ok_input" type="text" value="使用优惠券" />
								</div>
							</div>


